<template>
  <div id="app">
    <h1 class="cme-title">CORONAL MASS EJECTIONS</h1>
    <div v-if="status == 'loading'">
      <div class="loader"></div>
    </div>
    <div v-else-if="status == 'fetched'">
      <h3>Fetched!</h3>
    </div>
    <div v-else-if="status == 'begun'">
      <h3>Loading may take a while</h3>
    </div>
    <div class="all-nasa-data">
      <div v-if="!searched">
        <select v-model="startYear" class="start-year">
          <option value="2010">2010</option>
          <option value="2011">2011</option>
          <option value="2012">2012</option>
          <option value="2013">2013</option>
          <option value="2014">2014</option>
          <option value="2015">2015</option>
          <option value="2016">2016</option>
          <option value="2017">2017</option>
          <option value="2018">2018</option>
          <option value="2019">2019</option>
        </select>
        <select v-model="endYear" class="end-year">
          <option value="2010">2010</option>
          <option value="2011">2011</option>
          <option value="2012">2012</option>
          <option value="2013">2013</option>
          <option value="2014">2014</option>
          <option value="2015">2015</option>
          <option value="2016">2016</option>
          <option value="2017">2017</option>
          <option value="2018">2018</option>
          <option value="2019">2019</option>
        </select>
        <button v-on:click="getYear()">Fetch</button>
      </div>
      <div v-else></div>
      <div v-for="data in cmeData" :key="data.link" class="cme">
        <h1>
          Date:
          <span>{{ data.associatedCMEID }}</span>
        </h1>
        <h1>
          Latitude:
          <span>{{ data.latitude }}</span>
        </h1>
        <h1>
          Longitude:
          <span>{{ data.longitude }}</span>
        </h1>
        <h1>
          Type:
          <span>{{ data.type }}</span>
        </h1>
        <h1>
          Speed:
          <span>{{ data.speed }}</span> kps
        </h1>
      </div>
    </div>
    <div v-for="photo in photos" :key="photo.url" class="apto">
      <h1 class="day">PICTURE OF THE DAY</h1>
      <h2 class="title">{{ photo.title }}</h2>
      <img :src="photo.url" alt />
      <p class="explanation">{{ photo.explanation }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      cmeData: [],
      photos: [],
      startYear: "",
      endYear: "",
      status: "begun",
      searched: false,
    };
  },
  methods: {
    getYear: async function () {
      this.searched = true;
      try {
        this.cmeData = [];
        this.status = "loading";
        const res = await fetch(
          "https://api.nasa.gov/DONKI/CMEAnalysis?startDate=" +
            this.startYear +
            "-01-01&endDate=" +
            this.endYear +
            "-12-30&mostAccurateOnly=true&speed=500&halfAngle=30&catalog=ALL&api_key=99NoAROFiA9UdrcGHfGO66Ek2oDpAki5KXg5jlde"
        );
        const data = await res.json();
        this.cmeData = data;
        this.status = "fetched";
      } catch (err) {
        this.status = 'begun';
        alert(
          "Could not fetch. Make sure the ending year isn't lower than the starting year."
        );
        this.searched = false;
      }
    },
  },
  mounted: async function () {
    try {
      const res = await fetch(
        "https://api.nasa.gov/planetary/apod?api_key=99NoAROFiA9UdrcGHfGO66Ek2oDpAki5KXg5jlde"
      );
      const data = await res.json();
      this.photos.push(data);
    } catch (error) {
      this.photos = [
        {
          explanation:
            "Could not get the picture of the day. Come back soon to see it!",
        },
      ];
    }
  },
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 30px;
}

* {
  padding: 0px;
  margin: 0px;
}
body {
  text-align: center;
}
.cme {
  border: 3px solid black;
  margin: 10px;
  text-align: center;
  box-shadow: 0 0 4px 2px;
  border-radius: 7px;
  padding: 7px;
  transition: all 500ms ease;
}
.cme:hover {
  transform: scale(1.03);
  cursor: pointer;
  box-shadow: 0 0 10px 2px;
}
.cme-title {
  font-size: 50px;
}
.all-nasa-data {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.explanation {
  font-size: 26px;
  margin-top: 3px;
  margin-bottom: 10px;
}
.title {
  font-size: 40px;
}
.day {
  font-size: 50px;
}
.apto {
  border: 5px solid black;
  border-radius: 10px;
  margin-bottom: 5px;
  margin-top: 20px;
}
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
  position: relative;
  margin: 0 auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>



